import {
    CalendarToday,
    LocationSearching,
    MailOutline,
    PermIdentity,
    PhoneAndroid,
} from "@material-ui/icons";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import app from "../../firebase";
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage"
import { getUser, stateGetProduct, stateUpdateUser, updateUser } from "../../redux/apiCalls";
import { useEffect, useRef, useState } from "react";
import { AES, enc } from 'crypto-js'
import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router";
import { Link } from "react-router-dom";
import noAvatar from "../../images/noavatar.jpg"
import "./user.css";

export default function User() {
    const [file, setFile] = useState(null)
    const [pass, setPass] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const [address, setAddress] = useState("");
    const [errors1, setErrors1] = useState({ pass })
    const [errors2, setErrors2] = useState({ email })
    const [errors3, setErrors3] = useState({ phone })
    const [errors4, setErrors4] = useState({ address })
    const [values, setValues] = useState({
        password: '',
        email: '',
        phone: '',
        address: '',
    });
    const [sukses, setSukses] = useState(false);
    const dispatch = useDispatch();
    const location = useLocation();
    const playerId = location.pathname.split("/")[2];
    const adminRef = useRef();
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const player = useSelector((state) => state.getUser.getUser);

    const editAUser = useSelector((state) => state.editUsers.editUser)

    const pax = player !== null ? AES.decrypt(player.password, process.env.REACT_APP_PASS_SEC).toString(enc.Utf8) : '';

    useEffect(() => {
        stateGetProduct(dispatch);
        getUser(playerId, dispatch);
        if (editAUser?.response === 1) {
            stateUpdateUser(dispatch);
            getUser(playerId, dispatch);
        }
    }, [dispatch, playerId, editAUser]);

    const handleChange1 = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors1({ pass: '' })
        setPass(e.target.value);
        let reg2 = new RegExp(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,16}$/).test(e.target.value)
        if (!reg2) {
            setErrors1({ pass: "Password should be 6-16 characters, and atleast containt 1 Number" })
        }
    }

    const handleChange2 = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors2({ email: '' })
        setEmail(e.target.value)
        let reg1 = new RegExp(/^[^\s@]+@[^\s@]+\.[^\s@]+$/).test(e.target.value)
        if (!reg1) {
            setErrors2({ email: "Email should be a Valid Email and containt 1 '@ + .com' Character" })
        }
    }

    const handleChange3 = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors3({ phone: '' })
        setPhone(e.target.value)
        let reg = new RegExp(/^[0-9]{12,12}$/).test(e.target.value)
        if (!reg) {
            setErrors3({ phone: 'Phone should be only 12 Numbers' })
        }
    }

    const handleChange4 = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors4({ address: '' })
        setAddress(e.target.value)
    }

    const handleClose = () => {
        setSukses(false);
    }

    const handleClick = (e) => {
        e.preventDefault();
        if (!values.email) {
            setErrors2({ email: "Email is Required" })
        }
        if (!values.phone) {
            setErrors3({ phone: "Phone is Required" })
        }
        if (!values.address) {
            setErrors4({ address: "Address is Required" })
        }

        if (values.email && values.phone && values.address) {
            if (file) {
                const fileName = new Date().getTime() + file.name;
                const storage = getStorage(app);
                const StorageRef = ref(storage, fileName);
                const uploadTask = uploadBytesResumable(StorageRef, file);

                // Register three observers:
                // 1. 'state_changed' observer, called any time the state changes
                // 2. Error observer, called on failure
                // 3. Completion observer, called on successful completion
                uploadTask.on('state_changed', function (snapshot) {
                    // Observe state change events such as progress, pause, and resume
                    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                    const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    console.log('Upload is ' + progress + '% done');
                    switch (snapshot.state) {
                        case 'paused':
                            console.log('Upload is paused');
                            break;
                        case 'running':
                            console.log('Upload is running');
                            break;
                        default:
                    }
                }, function (error) {
                    // Handle unsuccessful uploads
                }, function () {
                    // Handle successful uploads on complete
                    // For instance, get the download URL: https://firebasestorage.googleapis.com/...
                    getDownloadURL(uploadTask.snapshot.ref).then(function (downloadURL) {
                        updateUser(playerId, {
                            ...values,
                            image: downloadURL,
                            isAdmin: adminRef.current.value,
                        }, dispatch)
                        setSukses(true);
                    });
                });
            } else {
                updateUser(playerId, {
                    ...values,
                    isAdmin: adminRef.current.value,
                }, dispatch)
                setSukses(true)
            }
        }
    }

    useEffect(() => {
        if (player !== null && Object.keys(player).length > 0) {
            setPass(pax || '')
            setEmail(player?.email || '')
            setPhone(player?.phone || '')
            setAddress(player?.address || '')
            setValues({
                password: pax || '',
                email: player?.email || '',
                phone: player?.phone || '',
                address: player?.address || ''
            })
        }
    }, [player, pax])

    return (
        <div className="user">
            <>
                <Dialog
                    fullScreen={fullScreen}
                    open={sukses}
                    onClose={handleClose}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Success"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            User Has been Successfully Updated!
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} autoFocus>
                            Close
                        </Button>
                    </DialogActions>
                </Dialog>
            </>
            <div className="userTitleContainer">
                <h1 className="userTitle">Edit User</h1>
                <Link to="/newUser">
                    <button className="userAddButton">Create</button>
                </Link>
            </div>
            <div className="userContainer">
                <div className="userShow">
                    <div className="userShowTop">
                        <img
                            src={player?.image || noAvatar}
                            alt=""
                            className="userShowImg"
                        />
                        <div className="userShowTopTitle">
                            <span className="userShowUsername">{player?.username}</span>
                            <span className="userShowUserTitle">Happy Me</span>
                        </div>
                    </div>
                    <div className="userShowBottom">
                        <span className="userShowTitle">Account Details</span>
                        <div className="userShowInfo">
                            <PermIdentity className="userShowIcon" />
                            <span className="userShowInfoTitle">{player?.username}</span>
                        </div>
                        <div className="userShowInfo">
                            <CalendarToday className="userShowIcon" />
                            <span className="userShowInfoTitle">10.12.1999</span>
                        </div>
                        <span className="userShowTitle">Contact Details</span>
                        <div className="userShowInfo">
                            <PhoneAndroid className="userShowIcon" />
                            <span className="userShowInfoTitle">{player?.phone}</span>
                        </div>
                        <div className="userShowInfo">
                            <MailOutline className="userShowIcon" />
                            <span className="userShowInfoTitle">{player?.email}</span>
                        </div>
                        <div className="userShowInfo">
                            <LocationSearching className="userShowIcon" />
                            <span className="userShowInfoTitle">{player?.address}</span>
                        </div>
                    </div>
                </div>
                <div className="userUpdate">
                    <span className="userUpdateTitle">Edit</span>
                    <form className="userUpdateForm">
                        <div className="userUpdateLeft">
                            <div className="userUpdateItem">
                                <TextField
                                    id="standard-basic1"
                                    label="Password"
                                    variant="standard"
                                    type="password"
                                    name="password"
                                    value={pass}
                                    placeholder="New Password"
                                    onChange={handleChange1}
                                    error={Boolean(errors1?.pass)}
                                    helperText={(errors1?.pass)}
                                />
                            </div>
                            <div className="userUpdateItem">
                                <TextField
                                    id="standard-basic2"
                                    label="Email"
                                    variant="standard"
                                    type="email"
                                    name="email"
                                    value={email}
                                    placeholder={player?.email || ""}
                                    onChange={handleChange2}
                                    error={Boolean(errors2?.email)}
                                    helperText={(errors2?.email)}
                                />
                            </div>
                            <div className="userUpdateItem">
                                <TextField
                                    id="standard-basic3"
                                    label="Phone"
                                    variant="standard"
                                    type="phone"
                                    name="phone"
                                    value={phone}
                                    placeholder={player?.phone || ""}
                                    onChange={handleChange3}
                                    error={Boolean(errors3?.phone)}
                                    helperText={(errors3?.phone)}
                                />
                            </div>
                            <div className="userUpdateItem">
                                <TextField
                                    id="standard-basic4"
                                    label="Address"
                                    variant="standard"
                                    type="address"
                                    name="address"
                                    value={address}
                                    placeholder={player?.address || ""}
                                    onChange={handleChange4}
                                    error={Boolean(errors4?.address)}
                                    helperText={(errors4?.address)}
                                />
                            </div>
                            <div className="userUpdateItem">
                                <label>Admin</label>
                                <select
                                    className="newUserSelect"
                                    name="isAdmin"
                                    id="active"
                                    ref={adminRef}
                                >
                                    <option value="false">No</option>
                                    <option value="true">Yes</option>
                                </select>
                            </div>
                        </div>
                        <div className="userUpdateRight">
                            <div className="userUpdateUpload">
                                <img
                                    className="userUpdateImg"
                                    src={player?.image || noAvatar}
                                    alt=""
                                />
                            </div>
                            <input className="uploadInput" type="file" id="file" onChange={e => setFile(e.target.files[0])} />
                            <button className="userUpdateButton" onClick={handleClick}>Update</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}
